.reply-arrow {
  width: 0; 
  height: 0; 
  border-left: 0.12rem solid transparent; 
  border-right: 0.12rem solid transparent; 
  border-bottom: 0.12rem solid #f5f5f9; 
  margin-left: 0.1rem;
}

.reply-contents {
  background-color: #f5f5f9;
  border-radius: 0.05rem;
}

.reply-item {
  padding: 0.1rem;
}

.reply-delete {
  font-size: 0.24rem;
  font-weight: 300;
  color: #f30;
  padding-left: 0.2rem;
}

.reply-time {
  font-size: 0.24rem;
  font-weight: 300;
  color: #333;
  padding-left: 0.2rem;
}


